.register-page {
    text-align: center;
    margin-top: 50px;
 
    legend {
       font-size: 34px;
       font-weight: bold;
       padding-bottom: 20px;
    }
 
    label {
       font-size: 22px;
       padding-bottom: 10px;
    }
 
    i {
       font-size: 27px;
    }
 
    .button {
       margin: 30px 0;
       font-size: 22px;
       padding: 10px 20px;
       border-radius: 8px;
       transition: color 0.5s ease-in;
       transition: background-color 0.5s ease-in;
    }
 
    .button:hover {
       background-color: black;
       color: white;
       transition: color 0.5s ease-in;
       transition: background-color 0.5s ease-in;
    }
 }
 